/* eslint no-undef: 0 */
/* eslint arrow-parens: 0 */
import React from 'react';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import drizzle from '../smart_contract/drizzle';
import {withTranslation} from 'react-i18next';

import Home from '../pages/home';
import TableA from '../pages/plans/table_a';
import FormA from '../pages/plans/form_a';
import TableB from '../pages/plans/table_b';
import FormB from '../pages/plans/form_b';
import MyNode from '../pages/my_node';
import MyContract from '../pages/my_contract';
import Language from '../pages/language';
import AdminAssetPoll from '../pages/admin/asset_poll';
import AdminInvesetment from '../pages/admin/investment';
import Docs from '../pages/docs';

import {enquireScreen} from 'enquire-js';

import Nav0 from './Nav0';
import Footer0 from './Footer0';

import {
  Footer00DataSource,
} from './data.source';

import './less/antMotionStyle.less';
import {Route} from 'react-router-dom';
import {Card} from 'antd';

let isMobile;
enquireScreen((b) => {
  isMobile = b;
});

const {location} = window;

class Layout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      show: !location.port, // 如果不是 dva 2.0 请删除
    };
  }

  componentDidMount() {
    // 适配手机屏幕;
    enquireScreen((b) => {
      this.setState({isMobile: !!b});
    });
    // dva 2.0 样式在组件渲染之后动态加载，导致滚动组件不生效；线上不影响；
    /* 如果不是 dva 2.0 请删除 start */
    if (location.port) {
      // 样式 build 时间在 200-300ms 之间;
      setTimeout(() => {
        this.setState({
          show: true,
        });
      }, 500);
    }
    /* 如果不是 dva 2.0 请删除 end */
  }

  render() {
    const t = this.props.t;
    //const Content = this.props.children;
    const children = [
      <Nav0
          id="Nav0_0"
          key="Nav0_0"
          // dataSource={Nav00DataSource}
          isMobile={this.state.isMobile}
      />,
      <drizzleReactHooks.DrizzleProvider drizzle={drizzle}>
        <drizzleReactHooks.Initializer
            // // Optional `node` to render on errors. Defaults to `'Error.'`.
            error={
              <div className="home-page-wrapper content0-wrapper">
                <div className="home-page content0">
                  <div className="title-wrapper">
                    <h1 name="title"><span><p>{t('error')}</p></span></h1>
                    <h3 name="title"><span><p>{t('content1')}</p></span></h3>
                  </div>
                  <div className="content0-block-wrapper">
                    <Card loading='true'>
                    </Card>
                  </div>
                </div>
              </div>}
            // // Optional `node` to render while loading contracts and accounts. Defaults to `'Loading contracts and accounts.'`.
            loadingContractsAndAccounts={
              <div className="home-page-wrapper content0-wrapper">
                <div className="home-page content0">
                  <div className="title-wrapper">
                    <h1 name="title"><span><p>{t('loading_contracts_and_accounts')}</p></span></h1>
                    <h3 name="title"><span><p>{t('content2')}</p></span></h3>
                  </div>
                  <div className="content0-block-wrapper">
                    <Card loading='true'>
                    </Card>
                  </div>
                </div>
              </div>}
            // // Optional `node` to render while loading `web3`. Defaults to `'Loading web3.'`.
            loadingWeb3={
              <div className="home-page-wrapper content0-wrapper">
                <div className="home-page content0">
                  <div className="title-wrapper">
                    <h1 name="title"><span><p>{t('loading_web3')}</p></span></h1>
                    <h3 name="title"><span><p>{t('content2')}</p></span></h3>
                  </div>
                  <div className="content0-block-wrapper">
                    <Card loading='true'>
                    </Card>
                  </div>
                </div>
              </div>}
        >
          <Route
              key="Home0"
              exact path='/'
              component={Home}/>
          <Route
              key="Home0_ref"
              exact path='/ref/:ref'
              component={Home}/>
          <Route
              key="Home0_0"
              exact path='/home' component={Home}/>
          <Route
              key="TableA0_0"
              exact path='/plans/table_a' component={TableA}/>
          <Route
              key="FormA0_0"
              exact path='/plans/form_a' component={FormA}/>
          <Route
              key="TableB0_0"
              exact path='/plans/table_b' component={TableB}/>
          <Route
              key="FormB0_0"
              exact path='/plans/form_b' component={FormB}/>
          <Route
              key="MyNodeB0_0"
              exact path='/my_node' component={MyNode}/>
          <Route
              key="MyContract0_0"
              exact path='/my_contract' component={MyContract}/>
          <Route
              key="Docs0_0"
              exact path='/docs' component={Docs}/>,
          <Route
              key="Language0_0"
              exact path='/language' component={Language}/>
          <Route
              key="Admin0_0"
              exact path='/admin/asset_poll' component={AdminAssetPoll}/>
          <Route
              key="Admin0_1"
              exact path='/admin/investment' component={AdminInvesetment}/>
        </drizzleReactHooks.Initializer>
      </drizzleReactHooks.DrizzleProvider>,
      <Footer0
          id="Footer0_0"
          key="Footer0_0"
          dataSource={Footer00DataSource}
          isMobile={this.state.isMobile}
      />,
    ];
    return (
        <div
            className="templates-wrapper"
            ref={(d) => {
              this.dom = d;
            }}
        >
          {/* 如果不是 dva 2.0 替换成 {children} start */}
          {this.state.show && children}
          {/* 如果不是 dva 2.0 替换成 {children} end */}
        </div>
    );
  }
}
export default withTranslation('Layout')(Layout);
