import React, {useEffect, useState} from 'react';
import Banner3 from './Banner3';
import Content0 from './Content0';
import {enquireScreen} from 'enquire-js';
import {useDispatch} from 'react-redux';
import {SET_REF} from '../../store/actionTypes';

export default (props) => {
  const [isMobile, setIsMobile] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    const refByUrl = props.match.params.ref;
    if (refByUrl) {
      dispatch({
        type: SET_REF,
        value: refByUrl,
      });
    }
  },[props.match.params.ref,dispatch]);
  useEffect(() => {
    // 适配手机屏幕;
    enquireScreen((b) => {
      setIsMobile(!!b);
    });
    }, [isMobile]);
  return ([
    <Banner3
        id="Banner3_0"
        key="Banner3_0"
        isMobile={isMobile}
    />,
    <Content0
        id="Content0_0"
        key="Content0_0"
        // dataSource={Content00DataSource}
        isMobile={isMobile}
    />]);
}
