import React, {useCallback, useEffect, useState} from 'react';
import {
  Form,
  Slider,
  Input,
  InputNumber,
  Button,
  Radio,
} from 'antd';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import web3 from 'web3';
import {useDispatch, useSelector} from 'react-redux';
import {SET_REF} from '../../store/actionTypes';
import {useHistory} from 'react-router-dom';
import {useTranslation} from 'react-i18next';

export default (props) => {
  const history = useHistory();
  const {t} = useTranslation('Plans');
  const [loading, setLoading] = useState(false);

  const {drizzle} = drizzleReactHooks.useDrizzle();
  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => ({
    accounts: drizzleState.accounts,
  }));
  const inviter = useSelector(state => state.ref);
  const [amount, setAmount] = useState(1);
  const [maxAmount, setMaxAmount] = useState(5);
  const [plan, setPlan] = useState(3);

  const dispatch = useDispatch();

  const onAmountChange = useCallback((value) => {
    if (value > maxAmount) {
      setAmount(maxAmount.toString());
      return;
    }
    setAmount(value);
  }, [maxAmount]);
  const onButtonClickCallBack = useCallback(() => {
    (async () => {
      try {
        setLoading(true);
        await drizzle.contracts.Investment.methods.createOrderB(inviter, plan)
            .send({
              from: drizzleState.accounts[0],
              value: web3.utils.toWei(amount.toString()),
            });
        history.push('/plans/table_b');
        setLoading(false);
      } catch (e) {
        setLoading(false);
      }
    })();

  }, [amount, inviter, drizzle, drizzleState, history, plan]);
  useEffect(() => {
    const fetchUser = async () => {
      try {
        setLoading(true);
        const userId = await drizzle.contracts.UserModel.methods.getIdByAddress(
            drizzleState.accounts[0]).call();
        const user = await drizzle.contracts.UserModel.methods.getUser(userId)
            .call();
        if (user.refAddress) {
          dispatch({
            type: SET_REF,
            value: user.refAddress,
          });
        }
        setMaxAmount(5 - web3.utils.fromWei(user.currentBalance));
        setLoading(false);
      } catch (e) {
        setLoading(false);
      }
    };
    fetchUser();
  }, [drizzle, drizzleState]);
  return (
      <div className="home-page-wrapper content0-wrapper">
        <div className="home-page content0">
          <div className="title-wrapper">
            <h1 name="title"><span><p>Investment PLAN B</p></span></h1>
            <h3 name="title"><span><p><a
                href={"https://etherscan.io/address/"+drizzle.contracts.Investment.address}
                target="_blank">{drizzle.contracts.Investment.address}</a></p></span>
            </h3>
          </div>
          <div className="content0-block-wrapper">
            <Form>
              <Form.Item label={t('Amount')}>
                <InputNumber
                    min={0.5}
                    max={5}
                    step={0.1}
                    value={amount}
                    onChange={onAmountChange}/>
              </Form.Item>
              <Form.Item>
                <Slider
                    min={0.5}
                    max={5}
                    step={0.01}
                    marks={{
                      0.5: '0.5',
                      1: '1',
                      3: '3',
                      5: '5',
                    }}
                    onChange={onAmountChange}
                    value={amount}
                />
              </Form.Item>
              <Form.Item label={t('Inviter')}>
                <Input value={inviter}
                       // onChange={(e) => {
                       //   dispatch({
                       //     type: SET_REF,
                       //     value: e.target.value,
                       //   });
                       // }}
                />
              </Form.Item>
              <Form.Item>
                <Radio.Group value={plan} buttonStyle="solid" onChange={(e) => {
                  setPlan(e.target.value);
                }}>
                  <Radio.Button value={1}>15 {t('Days')} 15%</Radio.Button>
                  <Radio.Button value={2}>30 {t('Days')} 35%</Radio.Button>
                  <Radio.Button value={3}>45 {t('Days')} 55%</Radio.Button>
                </Radio.Group>
              </Form.Item>
              <Form.Item>
                <Button block type="primary" onClick={onButtonClickCallBack}
                        loading={loading}
                >Send</Button>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>);
};
