import React, {useEffect, useState, useCallback} from 'react';
import {List, Button, Input, Form} from 'antd';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import web3 from 'web3';
import {SET_REF} from '../../store/actionTypes';
// import ApiServer from '../../api_server';

const BN = web3.utils.BN;

export default () => {
  const [loading, setLoading] = useState(false);
  const [amountI2A, setAmountI2A] = useState(0);
  const [amountA2I, setAmountA2I] = useState(0);
  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => ({
    accounts: drizzleState.accounts,
  }));
  const {
    drizzle,
  } = drizzleReactHooks.useDrizzle();

  const I2ACallback = useCallback(
      async () => {
        try {
          setLoading(true);
          let amount = web3.utils.toWei(amountI2A.toString(), 'ether');
          await drizzle.contracts.Investment.methods.wd(amount).send({
            from: drizzleState.accounts[0],
          });
          setLoading(false);
        } catch (e) {
          setLoading(false);
        }
      },
      [drizzle, amountI2A],
  );

  const A2ICallback = useCallback(
      async () => {
        try {
          setLoading(true);
          let amount = web3.utils.toWei(amountA2I.toString(), 'ether');
          await drizzle.contracts.AssetPool.methods.wd(amount).send({
            from: drizzleState.accounts[0],
          });
          setLoading(false);
        } catch (e) {
          setLoading(false);
        }
      },
      [drizzle, amountA2I],
  );

  return (
      <div className="home-page-wrapper content0-wrapper">
        <div className="home-page content0">
          <div className="title-wrapper">
            <h1 name="title"><span><p>Admin</p></span></h1>
          </div>
          <div className="content0-block-wrapper">
            <List itemLayout="horizontal" loading={loading}>
              <List.Item
                  actions={[
                    <Button
                        block
                        type="primary"
                        onClick={I2ACallback}>提交</Button>,
                  ]}>
                <List.Item.Meta
                    title='从投资合约向资产合约拨付'
                    description={<Input
                    value={amountI2A}
                    onChange={(e) => {
                      setAmountI2A(e.target.value);
                    }}/>}
                />
              </List.Item>
              <List.Item
                  actions={[
                    <Button
                        block
                        type="primary"
                        onClick={A2ICallback}>提交</Button>,
                  ]}>
                <List.Item.Meta
                    title='从资产合约向投资合约拨付'
                    description={<Input
                    value={amountA2I}
                    onChange={(e) => {
                      setAmountA2I(e.target.value);
                    }}/>}
                />
              </List.Item>
            </List>
          </div>
        </div>
      </div>);
}
