import React, {useEffect, useState, useCallback} from 'react';
import {List, Button} from 'antd';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import web3 from 'web3';
import {useTranslation} from 'react-i18next';
import ApiServer from '../../api_server';
import copy from 'copy-to-clipboard';
import QRCode from 'qrcode-react';

const BN = web3.utils.BN;

export default () => {
  const {t} = useTranslation('MyContract');
  const [user, setUser] = useState({});
  const [loading, setLoading] = useState(false);
  const [inviteUrl, setInviteUrl] = useState('');

  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => {
    return {
      accounts: drizzleState.accounts,
    };
  });
  const {
    drizzle,
  } = drizzleReactHooks.useDrizzle();

  const fetchTableCallback = useCallback(
      async () => {
        try {
          setLoading(true);
          const userId = await drizzle.contracts.UserModel.methods.getIdByAddress(
              drizzleState.accounts[0]).call();
          const apiServer = new ApiServer();
          const response = await apiServer.getUser(userId);
          setUser(response.data.data);
          setInviteUrl('https://www.freecontract.io/#/ref/' +
              response.data.data.user_address);
          setLoading(false);
        } catch (e) {
          setLoading(false);
        }
      },
      [drizzle, drizzleState],
  );

  useEffect(() => {
    fetchTableCallback();
  }, [drizzle, drizzleState]);
  return (
      <div className="home-page-wrapper content0-wrapper">
        <div className="home-page content0">
          <div className="title-wrapper">
            <h1 name="title"><span><p>My Contract</p></span></h1>
            {inviteUrl == '' ? '' :
                [
                  <h2 name="title"><QRCode value={inviteUrl}/></h2>,
                  <h2 name="title"><span><p>{inviteUrl}</p></span>
                    <Button
                        type="primary"
                        onClick={() => {
                          copy(inviteUrl);
                        }}>{t('Copy')}
                    </Button></h2>,
                ]}
          </div>
          <div className="content0-block-wrapper">
            <List itemLayout="horizontal" loading={loading}>
              <List.Item>
                <List.Item.Meta
                    title={t('User_Level')}
                    description={
                      (() => {
                        switch (user.level) {
                          case 1:
                            return t('Gold');
                          case 2:
                            return t('Platinum');
                          case 3:
                            return t('Diamond');
                          default:
                            return t('Normal');
                        }
                      })()
                    }
                />
              </List.Item>
              <List.Item>
                <List.Item.Meta
                    title={t('Investment_amount')}
                    description={web3.utils.fromWei(
                        new BN(user.current_balance)) + ' ETH'}
                />
              </List.Item>
              {/*<List.Item>*/}
              {/*  <List.Item.Meta*/}
              {/*      title={t('Total_investment')}*/}
              {/*      description={web3.utils.fromWei(*/}
              {/*          new BN(user.total_balance)) +*/}
              {/*      ' ETH'}*/}
              {/*  />*/}
              {/*</List.Item>*/}
              {/*<List.Item>*/}
              {/*  <List.Item.Meta*/}
              {/*      title={t('Total_income')}*/}
              {/*      description={web3.utils.fromWei(new BN(user.total_profit)) +*/}
              {/*      ' ETH'}*/}
              {/*  />*/}
              {/*</List.Item>*/}
              <List.Item
                  actions={[
                    <Button
                        block
                        type="primary"
                        onClick={async () => {
                          if (user.remain_dyn_profit == 0) {
                            return;
                          }
                          try {
                            setLoading(true);
                            let apiServer = new ApiServer();
                            await apiServer.createDynamicProfitPayOrder(
                                user.remain_dyn_profit, user.user_address);
                            fetchTableCallback();
                            setLoading(false);
                          } catch (e) {
                            setLoading(false);
                          }
                        }}>{t('Withdraw')}
                    </Button>]}
              >
                <List.Item.Meta
                    title={t('Total_dymnamic_income(unpaid/paid)')}
                    description={web3.utils.fromWei(
                        new BN(user.remain_dyn_profit)) + '/' +
                    web3.utils.fromWei(new BN(user.total_dyn_profit)) + ' ETH'}
                />
              </List.Item>
              <List.Item>
                <List.Item.Meta
                    title={t('Total_node_balance')}
                    description={web3.utils.fromWei(
                        new BN(user.total_node_balance)) +
                    ' ETH'}
                />
              </List.Item>
              <List.Item>
                <List.Item.Meta
                    title={t('My_address')}
                    description={user.user_address}
                />
              </List.Item>
              <List.Item>
                <List.Item.Meta
                    title={t('Inviter_address')}
                    description={user.parent_address}
                />
              </List.Item>
              <List.Item>
                <List.Item.Meta
                    title={t('Children_count')}
                    description={user.children_count}
                />
              </List.Item>
              <List.Item>
                <List.Item.Meta
                    title={t('Descendants_count')}
                    description={user.descendants_count}
                />
              </List.Item>
            </List>
          </div>
        </div>
      </div>);
}
