import React, {useEffect, useState} from 'react';
import {Table} from 'antd';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import web3 from 'web3';
import ApiServer from '../../api_server';
import {useTranslation} from 'react-i18next';

export default () => {
  const {t} = useTranslation('Nodes');
  const columns = [
    // {
    //   title: t('User_ID'),
    //   dataIndex: 'id',
    // },
    // {
    //   title: t('Parent_ID'),
    //   dataIndex: 'parent_id',
    // },
    {
      title: t('Address'),
      dataIndex: 'user_address',
      responsive: ['md'],
    },
    {
      title: t('Investment_amount(ETH)'),
      dataIndex: 'current_balance',
    },
    {
      title: t('Total_investment(ETH)'),
      dataIndex: 'total_balance',
    },
    {
      title: t('Children_count'),
      dataIndex: 'children_count',
      responsive: ['md'],
    },
    {
      title: t('Descendants_count'),
      dataIndex: 'descendants_count',
      responsive: ['md'],
    },
  ];
  const [tableData, setTableData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });
  const onTableChange = (pagination, filters, sorter) => {
    setPagination(pagination);
  };

  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => {
    return {
      accounts: drizzleState.accounts,
    };
  });
  const {
    drizzle,
  } = drizzleReactHooks.useDrizzle();

  useEffect(() => {
    const fetchTable = async (pageId = 1, pageSize = null) => {
      try {
        setLoading(true);
        const userId = await drizzle.contracts.UserModel.methods.getIdByAddress(
            drizzleState.accounts[0]).call();
        const apiServer = new ApiServer();
        const response = await apiServer.getUserDescendants(userId, pageId);
        const data = response.data.data;
        const meta = response.data.meta;
        const pagination = {
          current: meta.current_page,
          pageSize: meta.per_page,
          total: meta.total,
          showSizeChanger: false,
          onChange: fetchTable,
        };
        let table = [];
        for (let i = 0; i < data.length; i++) {
          table.push({
                key: data[i].id,
                id: data[i].id,
                parent_id: data[i].parent_id,
                user_address: data[i].user_address,
                parent_address: data[i].parent_address,
                current_balance: web3.utils.fromWei(data[i].current_balance),
                total_balance: web3.utils.fromWei(data[i].total_balance),
                children_count: data[i].children_count,
                descendants_count: data[i].descendants_count,
              },
          );
        }
        // }
        // const inviteUsersCount = await drizzle.contracts.UserModel.methods.getInviteUsersLength(
        //     userId).call();
        // let inviteUserId;
        // let inviteUser;
        // let table = [];
        // for (let i = inviteUsersCount - 1; i >= 0; i--) {
        //   inviteUserId = await drizzle.contracts.UserModel.methods.getInviteUsersId(
        //       userId, i).call();
        //   inviteUser = await drizzle.contracts.UserModel.methods.getUser(
        //       inviteUserId).call();
        //   table.push({
        //     key: i,
        //     address: inviteUser.userAddress,
        //     currentBalance: web3.utils.fromWei(inviteUser.currentBalance),
        //     totalBalance: web3.utils.fromWei(inviteUser.totalBalance),
        //   });
        // }
        setTableData(table);
        setPagination(pagination);
        setLoading(false);
      } catch (e) {
        setLoading(false);
        console.log(e);
      }
    };
    fetchTable();
  }, [drizzle, drizzleState]);
  return (
      <div className="home-page-wrapper content0-wrapper">
        <div className="home-page content0">
          <div className="title-wrapper">
            <h1 name="title"><span><p>My Node</p></span></h1>
          </div>
          <div className="content0-block-wrapper">
            <Table columns={columns}
                   dataSource={tableData}
                   pagination={pagination}
                   loading={loading}
                   onChange={onTableChange}
            />
          </div>
        </div>
      </div>);
}
