import React, {useCallback, useEffect, useState} from 'react';
import web3 from 'web3';
import moment from 'moment';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import {
  Table,
  Button,
} from 'antd';

import {useHistory} from 'react-router-dom';
import {useTranslation} from 'react-i18next';

const BN = web3.utils.BN;
const PLAN_B_DAYS = [0, 15, 30, 45];
const PLAN_B_PROFIT = [0, 15, 35, 55];
const PLAN_B_NAME = ['', '15 Days 15%', '30 Days 35%', '45 Days 55%'];
const CloseButton = (props) => {
  const {t} = useTranslation('Plans');
  const [loading, setLoading] = useState(false);
  const [isDisable, setIsDisable] = useState(false);
  const [text, setText] = useState(t('Settle'));
  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => {
    return {
      accounts: drizzleState.accounts,
    };
  });
  const {
    drizzle,
  } = drizzleReactHooks.useDrizzle();
  useEffect(() => {
    if (props.isClose) {
      setIsDisable(true);
      setText(t('Settled'));
      return;
    }
    if (props.elapsedDay < PLAN_B_DAYS[props.plan]) {
      setIsDisable(true);
      setText(t('Unexpired'));
      return;
    }
  }, [props.elapsedDay, props.isClose, props.plan]);

  const closeOrder = useCallback(() => {
        (async () => {
          try {
            setLoading(true);
            await drizzle.contracts.Investment.methods.closeOrderB(props.orderId)
                .send({from: drizzleState.accounts[0]});
            setIsDisable(true);
            setText(t('Settled'));
            setLoading(false);
          } catch (e) {
            setLoading(false);
          }
        })();
      }
      , [drizzle, drizzleState, props.orderId]);

  return (
      <Button disabled={isDisable}
              onClick={() => closeOrder()}
              loading={loading}>
        {text}
      </Button>
  );
};
export default (props) => {
  const {t} = useTranslation('Plans');
  const columns = [
    {
      title: t('Investment_time'),
      dataIndex: 'openTime',
      responsive: ['md'],
    },
    {
      title: t('Elapsed_day'),
      dataIndex: 'elapsedDay',
      responsive: ['md'],
    },
    {
      title: t('Plan'),
      dataIndex: 'plan',
      responsive: ['md'],
    },
    {
      title: t('Amount(ETH)'),
      dataIndex: 'amount',
    },
    {
      title: t('Income(ETH)'),
      dataIndex: 'profit',
    },
    {
      title: t('Operating'),
      dataIndex: 'action',
    },
  ];
  const [tableData, setTableData] = useState([]);

  const [loading, setLoading] = useState(false);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
  });

  const onTableChange = (pagination, filters, sorter) => {
    setPagination(pagination);
  };

  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => {
    return {
      accounts: drizzleState.accounts,
    };
  });
  const {
    drizzle,
  } = drizzleReactHooks.useDrizzle();

  const history = useHistory();
  useEffect(() => {
    const fetchTable = async () => {
      setLoading(true);
      const userId = await drizzle.contracts.UserModel.methods.getIdByAddress(
          drizzleState.accounts[0]).call();
      const orderCount = await drizzle.contracts.User_InvestmentBetaOrderModel.methods.getInvestmentBetaOrdersLengthFromUser(
          userId).call();
      let order;
      let table = [];
      let elapsedDay;
      let amount;
      let profit;
      let orderId;
      for (let i = orderCount - 1; i >= 0; i--) {
        orderId = await drizzle.contracts.User_InvestmentBetaOrderModel.methods.getInvestmentBetaOrderIdFromUser(
            userId, i).call();
        order = await drizzle.contracts.InvestmentBetaOrderModel.methods.getInvestmentBetaOrder(
            orderId).call();
        elapsedDay = moment().diff(moment.unix(order.openTime), 'days');
        elapsedDay = elapsedDay > PLAN_B_DAYS[order.plan] ?
            PLAN_B_DAYS[order.plan] :
            elapsedDay;
        amount = new BN(order.amount);
        profit = amount.mul(new BN(PLAN_B_PROFIT[order.plan].toString())).div(new BN('100')).add(amount).toString();
        profit = web3.utils.fromWei(profit);
        table.push({
          key: i,
          openTime: moment.unix(order.openTime).format('YYYY.MM.DD'),
          amount: web3.utils.fromWei(order.amount),
          elapsedDay: elapsedDay,
          plan: PLAN_B_NAME[order.plan],
          profit: profit,
          action: <CloseButton key={i} elapsedDay={elapsedDay}
                               isClose={order.isClosed} plan={order.plan}
                               orderId={orderId}/>,
        });
      }
      setTableData(table);
      setLoading(false);
    };
    fetchTable();
  }, [drizzle, drizzleState]);
  // useEffect( ()=>{
  //   if(table !== undefined){
  //     setPagination({
  //       current: parseInt(table.orderCount/pagination.pageSize),
  //       pageSize: pagination.pageSize,
  //     })
  //   }
  //     //console.log(table);
  //
  //   //   console.log(drizzleState.web3);
  // //   console.log(userId,orderCount);
  //   // setPagination({
  //   //   current: parseInt(orderCount/pagination.pageSize),
  //   //   pageSize: pagination.pageSize,
  //   // })
  //   // for(let i=0;i<orderCount;i++){
  //   //
  //   // }
  // },[pagination]);

  return (
      <div className="home-page-wrapper content0-wrapper">
        <div className="home-page content0">
          <div className="title-wrapper">
            <h1 name="title"><span><p>Investment PLAN B</p></span></h1>
            <h2>
              <Button type="primary" onClick={() => {
                history.push('/plans/form_b');
              }}>
                {t('Join_Plan_B')}
              </Button></h2>
          </div>
          <div className="content0-block-wrapper">
            <Table columns={columns}
                   dataSource={tableData}
                   pagination={pagination}
                   loading={loading}
                   onChange={onTableChange}/>
          </div>
        </div>
      </div>);
};
