import React, {useCallback, useEffect, useState} from 'react';
import web3 from 'web3';
import moment from 'moment';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import {
  Table,
  Button,
} from 'antd';

import {useHistory} from 'react-router-dom';
import { useTranslation } from 'react-i18next';

const BN=web3.utils.BN;
const PLAN_A_PROFITS_PER_MILLE = [0,1,3,6,10,15,21,28,36,45,55,66,78,91,105,120];

const CloseButton = (props) => {
  const {t} = useTranslation('Plans');
  const [loading, setLoading] = useState(false);
  const [isDisable,setIsDisable] = useState(false);
  const [text,setText] = useState(t('Settle'));
  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => {
    return {
      accounts: drizzleState.accounts,
    };
  });
  const {
    drizzle,
  } = drizzleReactHooks.useDrizzle();
  useEffect(()=>{
    if(props.isClose)
    {
      setIsDisable(true);
      setText(t('Settled'));
      return;
    }
    if(props.elapsedDay<1)
    {
      setIsDisable(true);
      setText(t('Unexpired'));
      return;
    }
  },[props.elapsedDay,props.isClose])

  const closeOrder = useCallback(() => {
        (async () => {
          try{
            setLoading(true);
            await drizzle.contracts.Investment.methods.closeOrderA(props.orderId)
                .send({from: drizzleState.accounts[0]});
            setIsDisable(true);
            setText(t('Settled'));
            setLoading(false);
          }catch (e) {
            setLoading(false);
          }
        })();
      }
      , [drizzle,drizzleState,props.orderId]);

  return (
      <Button disabled={isDisable}
              onClick={() => closeOrder(props.orderId)}
              loading={loading}>
        {text}
      </Button>
  );
};
