import { SET_REF } from './actionTypes'

const defaultState = {
  ref : "0x0000000000000000000000000000000000000000",
}  //默认数据
export default (state = defaultState,action)=>{
  //就是一个方法函数
  switch (action.type) {
    case SET_REF:
      return {
        ...state,
        ref:action.value,
      }
    default:
      return state;

  }
}
