import React from 'react';
import {Button} from 'antd';
import QueueAnim from 'rc-queue-anim';
import Texty from 'rc-texty';
import 'rc-texty/assets/index.css';
import {withTranslation} from 'react-i18next';

class Banner extends React.PureComponent {
  render() {
    const t = this.props.t;
    const {...currentProps} = this.props;
    const dataSource = {
      wrapper: {className: 'banner3 kasbayqqg8-editor_css'},
      textWrapper: {
        className: 'banner3-text-wrapper',
        children: [
          {
            name: 'nameEn',
            className: 'banner3-name-en',
            children: (
                <span>
            <p>{t('Free_Transparent_Open_Secure')}</p>
          </span>
            ),
          },
          {
            name: 'slogan',
            className: 'banner3-slogan',
            children: 'FREE CONTRACT',
            texty: true,
          },
          {
            name: 'name',
            className: 'banner3-name',
            children: (
                <span>
            <span>
              <p>{t('be_free_feel_free')}</p>
            </span>
          </span>
            ),
          },
          {
            name: 'button',
            className: 'banner3-button kasb7valcy-editor_css',
            children:
            <a href="#/plans/form_a">
                <span>
                  <p>{t('Join_Now')}</p>
                </span>
            </a>
          },
        ],
      },
    };
    delete currentProps.dataSource;
    delete currentProps.isMobile;
    const children = dataSource.textWrapper.children.map((item) => {
      const {name, texty, ...$item} = item;
      if (name.match('button')) {
        return (
            <Button type="primary" key={name} {...$item}>
              {item.children}
            </Button>
        );
      }

      return (
          <div key={name} {...$item}>
            {texty ? (
                <Texty type="mask-bottom">{item.children}</Texty>
            ) : (
                item.children
            )}
          </div>
      );
    });
    return (
        <div {...currentProps} {...dataSource.wrapper}>
          <QueueAnim
              key="QueueAnim"
              type={['bottom', 'top']}
              delay={200}
              {...dataSource.textWrapper}
          >
            {children}
          </QueueAnim>
        </div>
    );
  }
}

export default withTranslation('Banner')(Banner);
