import React, {useEffect, useState, useCallback} from 'react';
import {List, Button, Input, Form} from 'antd';
import {drizzleReactHooks} from '@drizzle/react-plugin';
import web3 from 'web3';
import {SET_REF} from '../../store/actionTypes';
// import ApiServer from '../../api_server';

const BN = web3.utils.BN;

export default () => {
  const [loading, setLoading] = useState(false);
  const [amountWDA, setAmountWDA] = useState(0);
  const drizzleState = drizzleReactHooks.useDrizzleState(drizzleState => ({
    accounts: drizzleState.accounts,
  }));
  const {
    drizzle,
  } = drizzleReactHooks.useDrizzle();

  const WDACallback = useCallback(
      async () => {
        try {
          setLoading(true);
          let amount = web3.utils.toWei(amountWDA.toString(),'ether');
          console.log(amount);
          await drizzle.contracts.AssetPool.methods.wd(amount).send({
            from: drizzleState.accounts[0],
          });
          console.log(amount);
          setLoading(false);
        } catch (e) {
          setLoading(false);
        }
      },
      [drizzle, amountWDA],
  );

  return (
      <div className="home-page-wrapper content0-wrapper">
        <div className="home-page content0">
          <div className="title-wrapper">
            <h1 name="title"><span><p>Admin</p></span></h1>
          </div>
          <div className="content0-block-wrapper">
            <List itemLayout="horizontal" loading={loading}>
              <List.Item
                  actions={[
                    <Button
                        block
                        type="primary"
                        onClick={WDACallback}>提交</Button>,
                  ]}>
                <List.Item.Meta
                    title='从资产合约中提款'
                    description={<Input
                        value={amountWDA}
                        onChange={(e) => {
                          setAmountWDA(e.target.value);
                        }}/>}
                />
              </List.Item>
            </List>
          </div>
        </div>
      </div>);
}
