import React from 'react';
import TweenOne from 'rc-tween-one';
import {Menu} from 'antd';
import {getChildrenToRender} from './utils';
import {withTranslation} from 'react-i18next';

const {Item, SubMenu} = Menu;

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      phoneOpen: undefined,
    };
  }

  phoneClick = () => {
    const phoneOpen = !this.state.phoneOpen;
    this.setState({
      phoneOpen,
    });
  };

  render() {
    const t = this.props.t;
    const dataSource = {
      wrapper: {className: 'header0 home-page-wrapper kasgmta48yf-editor_css'},
      page: {className: 'home-page kasgd28btxd-editor_css'},
      logo: {
        className: 'header0-logo',
        children: require('../imgs/logo.png'),
      },
      Menu: {
        className: 'header0-menu kasgm4g5ygb-editor_css',
        children: [
          {
            name: 'item0',
            className: 'header0-item kasg99bwlgd-editor_css',
            children: {
              href: '#/home',
              children: [
                {
                  children: (
                      <span>
                  <p>{t('Home')}</p>
                </span>
                  ),
                  name: 'text',
                  className: 'kasg9r2phet-editor_css',
                },
              ],
            },
            subItem: null,
          },
          {
            name: 'item1',
            className: 'header0-item',
            children: {
              children: [
                {
                  children: (
                      <span>
                  <p>{t('My_Investments')}</p>
                </span>
                  ),
                  name: 'text',
                },
              ],
            },
            subItem: [
              {
                className: 'item-sub kash208ln5-editor_css',
                children: {
                  href: '#/plans/table_a',
                  className: 'item-sub-item',
                  children: [
                    {
                      name: 'image0',
                      className: 'item-image',
                      children:
                      <img src={require('../imgs/logo30.png')}/>
                    },
                    {
                      name: 'title',
                      className: 'item-title',
                      children: 'Investment PLAN A',
                    },
                    {
                      name: 'content',
                      className: 'item-content',
                      children: 'PLAN A',
                    },
                  ],
                },
              },
              {
                className: 'item-sub kash208ln5-editor_css',
                children: {
                  href: '#/plans/table_b',
                  className: 'item-sub-item',
                  children: [
                    {
                      name: 'image0',
                      className: 'item-image',
                      children:
                          <img src={require('../imgs/logo30.png')}/>
                    },
                    {
                      name: 'title',
                      className: 'item-title',
                      children: 'Investment PLAN B',
                    },
                    {
                      name: 'content',
                      className: 'item-content',
                      children: 'PLAN B',
                    },
                  ],
                },
              },
            ],
          },
          {
            name: 'item3',
            className: 'header0-item kasgmpy1ah-editor_css',
            children: {
              href: '#/my_contract',
              children: [
                {
                  children: (
                      <span>
                  <p>{t('My_Contracts')}</p>
                </span>
                  ),
                  name: 'text',
                },
              ],
            },
          },
          {
            name: 'item2',
            className: 'header0-item',
            children: {
              href: '#/my_node',
              children: [
                {
                  children: (
                      <span>
                  <p>{t('My_Nodes')}</p>
                </span>
                  ),
                  name: 'text',
                },
              ],
            },
          },
          {
            name: 'item4',
            className: 'header0-item kasgmpy1ah-editor_css',
            children: {
              href: '#/docs',
              children: [
                {
                  children: (
                      <span>
                  <p>{t('Docs')}</p>
                </span>
                  ),
                  name: 'text',
                },
              ],
            },
          },
          {
            name: 'item5',
            className: 'header0-item kasgmpy1ah-editor_css',
            children: {
              href: '#/language',
              children: [
                {
                  children: (
                      <span>
                  <p>{t('Language')}</p>
                </span>
                  ),
                  name: 'text',
                },
              ],
            },
          }
        ],
      },
      mobileMenu: {className: 'header0-mobile-menu'},
    };
    const {isMobile, ...props} = this.props;
    delete props.t;
    delete props.tReady;
    const {phoneOpen} = this.state;
    const navData = dataSource.Menu.children;
    const navChildren = navData.map((item) => {
      const {children: a, subItem, ...itemProps} = item;
      if (subItem) {
        return (
            <SubMenu
                key={item.name}
                {...itemProps}
                title={
                  <div
                      {...a}
                      className={`header0-item-block ${a.className}`.trim()}
                  >
                    {a.children.map(getChildrenToRender)}
                  </div>
                }
                popupClassName="header0-item-child"
            >
              {subItem.map(($item, ii) => {
                const {children: childItem} = $item;
                const child = childItem.href ? (
                    <a {...childItem}>
                      {childItem.children.map(getChildrenToRender)}
                    </a>
                ) : (
                    <div {...childItem}>
                      {childItem.children.map(getChildrenToRender)}
                    </div>
                );
                return (
                    <Item key={$item.name || ii.toString()} {...$item}>
                      {child}
                    </Item>
                );
              })}
            </SubMenu>
        );
      }
      return (
          <Item key={item.name} {...itemProps}>
            <a {...a} className={`header0-item-block ${a.className}`.trim()}>
              {a.children.map(getChildrenToRender)}
            </a>
          </Item>
      );
    });
    const moment = phoneOpen === undefined ? 300 : null;
    return (
        <TweenOne
            component="header"
            animation={{opacity: 0, type: 'from'}}
            {...dataSource.wrapper}
            {...props}
        >
          <div
              {...dataSource.page}
              className={`${dataSource.page.className}${phoneOpen ?
                  ' open' :
                  ''}`}
          >
            <TweenOne
                animation={{x: -30, type: 'from', ease: 'easeOutQuad'}}
                {...dataSource.logo}
            >
              <img width="100%" src={dataSource.logo.children} alt="img"/>
            </TweenOne>
            {isMobile && (
                <div
                    {...dataSource.mobileMenu}
                    onClick={() => {
                      this.phoneClick();
                    }}
                >
                  <em/>
                  <em/>
                  <em/>
                </div>
            )}
            <TweenOne
                className='header0-menu kasgm4g5ygb-editor_css'
                animation={
                  isMobile
                      ? {
                        height: 0,
                        duration: 300,
                        onComplete: (e) => {
                          if (this.state.phoneOpen) {
                            e.target.style.height = 'auto';
                          }
                        },
                        ease: 'easeInOutQuad',
                      }
                      : null
                }
                moment={moment}
                reverse={!!phoneOpen}
            >
              <Menu
                  mode={isMobile ? 'inline' : 'horizontal'}
                  defaultSelectedKeys={['sub0']}
                  theme="dark"
              >
                {navChildren}
              </Menu>
            </TweenOne>
          </div>
        </TweenOne>
    );
  }
}

export default withTranslation('Nav')(Header);
